.App-weather {
  display: block;
  margin: 8px auto;
  text-align: center;
  max-width: 400px;

}
.Header-logo {
  display: block; 
  height: 56px;
}
.weather-icon {
  float: left;
  width: 50%;
}
.weather-icon img {
  float: right;
  margin-top: -20px;
}
#temperature {
  font-size: 3em;
}
#main {
  font-size: 1.5em;
  margin-top: 8px;
}
#humidity {
  display: block;
  margin-top: 30px;
  font-size: 1.1em;
  text-align: center;
}
.forecast {
  display: block;
  max-width: 500px;
  margin: 0 auto;
}
.forecast ul {
  display: inline-block;
}
.forecast ul li {
  list-style: none;
  float: left;
  margin: 18px
}



